@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
.container{width:100%;max-width:300px;margin:0 auto}.container .row{width:95%;margin:0 auto;height:100%;display:flex;justify-content:center}.container .row [class*='col-']{min-height:1px}.container .row .col-1{width:8.33333%}.container .row .col-2{width:16.66667%}.container .row .col-3{width:25%}.container .row .col-4{width:33.33333%}.container .row .col-5{width:41.66667%}.container .row .col-6{width:50%}.container .row .col-7{width:58.33333%}.container .row .col-8{width:66.66667%}.container .row .col-9{width:75%}.container .row .col-10{width:83.33333%}.container .row .col-11{width:91.66667%}.container .row .col-12{width:100%}*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Lato",sans-serif;font-weight:400}a{text-decoration:none;color:#000}

.hero{background-color:grey;position:absolute;height:100vh;width:100%;background-position:center;background-size:cover;-webkit-filter:brightness(60%);filter:brightness(60%);z-index:-1}

.fullForm{height:100vh}.fullForm .header{flex-direction:column;color:#fff}.fullForm .header .header__title{font-size:2em;line-height:2em;font-weight:900}.fullForm .header .header__text{margin:5px 0}.fullForm .header .header__text:last-of-type{font-size:0.9em}.fullForm .header .header__text a{color:#fff;text-decoration:underline;background-image:url()}.fullForm .header .header__form{display:flex;align-items:center;position:relative;margin:20px 0}.fullForm .header .header__form .fas{background-color:#fff;color:grey;position:absolute}.fullForm .header .header__form .fa-search{left:15px}.fullForm .header .header__form .fa-times{right:15px}.fullForm .header .header__form .header__form__input{width:100%;padding:10px 10px 10px 40px}

